$bc: lightgray;
.card {
  max-width: 15rem;
  max-height: 17rem;
  border: 1px solid $bc;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12),
    0px 2px 4px rgba(0, 0, 0, 0.14);
  border-radius: 4px;
  flex-basis: 18%;
  @media (max-width: 590px) {
    max-width: 316px;
    max-height: 18rem;
  }
}
img {
  width: 100%;
  max-height: 70%;
  border-radius: 4px 4px 0 0;
}
.responsive {
  max-width: 60%;
}
.description {
  margin: 0 0 0 1rem;
}
.p {
  margin: 0.3rem 0 0 0;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.6);
}
.h3 {
  margin: 0.5rem 0 0 0;
  font-size: calc(8px + 6 * ((100vw - 320px) / 680));
  font-weight: 500;
  font-size: 18px;
  line-height: 30px;
}
